<template>
  <div class="p2">
    <P class="pp1">印花税纳税申报表</P>
    <form>
      <table class="yhs" cellpadding="0" width="100%" cellspacing="0" border="1">
        <tr>
          <td style="background-color:#d3e2ee; width:150px;">纳税人识别号</td>
          <td colspan="2">{{com.firm_code}}</td>
          <td colspan="2" style="background-color:#d3e2ee;">纳税人名称</td>
          <td colspan="2">{{com.firm_name}}</td>
          <td style="background-color:#d3e2ee;">申报日期</td>
          <td colspan="2">{{com.month}}-{{new Date().getDate()}}</td>
        </tr>
        <tr>
          <td style="background-color:#d3e2ee;">税款所属期起</td>
          <td colspan="2">{{com.month}}-01</td>
          <td colspan="2" style="background-color:#d3e2ee;">税款所属期止</td>
          <td colspan="2">{{com.month}}-30</td>
          <td style="background-color:#d3e2ee;">申报类型</td>
          <td colspan="2">正常申报</td>
        </tr>
        <tr>
          <td style="background-color:#d3e2ee;">纳税人类型</td>
          <td colspan="2">单位</td>
          <td colspan="2" style="background-color:#d3e2ee;">登记注册类型</td>
          <td colspan="2">{{com.firm_type}}</td>
          <td style="background-color:#d3e2ee;">所属行业</td>
          <td colspan="2">{{com.firm_trade}}</td>
        </tr>
        <tr>
          <td style="background-color:#d3e2ee;">身份证件类型</td>
          <td colspan="2">居民身份证</td>
          <td colspan="2" style="background-color:#d3e2ee;">身份证件号码</td>
          <td colspan="2">{{com.firm_legal_person_id}}</td>
          <td>联系方式</td>
          <td colspan="2" style="background-color:#FFF;">{{com.firm_telephone}}</td>
        </tr>
        <tr style="background-color:#d3e2ee;">
          <td rowspan="3">应税凭证名称</td>
          <td rowspan="2">计税金额或件数</td>
          <td colspan="2">核定征收</td>
          <td rowspan="2">适用税率</td>
          <td rowspan="2">本期应纳税额</td>
          <td rowspan="2">本期已缴税额</td>
          <td colspan="2">本期减免税额</td>
          <td rowspan="2">本期应补（退）税额</td>
        </tr>
        <tr style="background-color:#d3e2ee;">
          <td>核定计税依据</td>
          <td>核定比例</td>
          <td>减免性质代码</td>
          <td>减免税额</td>
        </tr>
        <tr style="background-color:#d3e2ee;">
          <td>1</td>
          <td>2</td>
          <td>3</td>
          <td>4</td>
          <td>5=1*4 or 5=1*3*4 or 5=2*4</td>
          <td>6</td>
          <td>7</td>
          <td>8</td>
          <td>9=5-6-8</td>
        </tr>

        <tr v-for="(item,index) in yhs" v-if="item.taxes_status === taxes" class="addtr">
          <td>{{ item.taxable_name }}</td>
          <td style="background-color:#FFF;">
            <input :name="index+1 + '_1'" value="0.00"/>
          </td>
          <td>
            <input :name="index+1 + '_2'" readonly value="0.00"/>
          </td>
          <td>
            <input :name="index+1 + '_3'" readonly class="tiaochu" value="0.00%"/>
          </td>
          <td>
            <input :name="index+1 + '_4'" readonly class="tiaochu" :value="item.tax_rate"/>
          </td>
          <td>
            <input :name="index+1 + '_5'" readonly value="0.00"/>
          </td>
          <td style="background-color:#FFF;">
            <input :name="index+1 + '_6'" value="0.00"/>
          </td>
          <td style="background-color:#FFF;">
            <input :name="index+1 + '_7'" type="text"/>
          </td>
          <td style="background-color:#FFF;">
            <input :name="index+1 + '_8'" value="0.00"/>
          </td>
          <td>
            <input :name="index+1 + '_9'" readonly value="0.00"/>
          </td>
        </tr>
        <tr>
          <td style="background-color:#d3e2ee;">合计</td>
          <td>--</td>
          <td>--</td>
          <td>--</td>
          <td>--</td>
          <td><input name="h_5" readonly value="0.00"/></td>
          <td><input name="h_6" readonly value="0.00"/></td>
          <td>--</td>
          <td><input name="h_8" readonly value="0.00"/></td>
          <td><input name="h_9" readonly value="0.00"/></td>
        </tr>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <router-link to="/transfer/p42" @click.native="saveForm">
        <button class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 申报</button>
      </router-link>
      <button class="uk-button uk-button-primary" @click="$router.go(-1)"><i class="uk-icon-close"></i> 关闭</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 41,  //当前表格的名称编号
        shuimu: [],
        com: [],
        taxes: this.$store.state.isConfirmed.taxes_status
      }
    },
    computed: {
      yhs() {
        return this.$store.state.companyCurrent.yhs
      },
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    created() {
      this.initData()
    },
    mounted() {
      this.getSetting()  //获取后台设置数据
    },
    //td被渲染完毕后执行的钩子
    updated() {
      this.inputChangeEvent()
      this.inputClickEvent()
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        //获取预设数据
        this.com = this.$store.state.companyCurrent.gscompany
        // this.setting = this.$store.state.companyCurrent.yhs;
        let arr = this.$store.state.companyCurrent.yhs
        var attr = []
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].taxes_status == this.taxes) {
            attr.push(arr[i])
          }
        }
        this.setting = attr
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn1(5, 4, [1, 4], 1)
        this.fn1(9, 4, [5, 6, 8], 2)
        this.fn2('h_5', 1)
        this.fn2('h_9', 2)
      },
      // q 要计算单元格所在的列
      // r 要运算多少行
      // jump 参与运算的列
      // type { 运算公式
      // 1:a=b*c+d*e*f*g
      // 2:a=b-c-d
      // }
      fn1(q, r, jump, type) {
        var sum = 0
        for (let i = 1; i <= r; i++) {
          if (type == 1) {
            sum = parseFloat(
              J('input[name=' + i + '_' + jump[0] + ']').val() * J('input[name=' + i + '_' + jump[1] + ']').val()
            )
          } else if (type == 2) {
            sum = parseFloat(
              J('input[name=' + i + '_' + jump[0] + ']').val() -
              J('input[name=' + i + '_' + jump[1] + ']').val() -
              J('input[name=' + i + '_' + jump[2] + ']').val())
          }
          J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
        }
      },
      // name 要计算单元格的位置
      // type 要运算的列
      // 1: 第5列总和计算
      fn2(name, type) {
        var tr_num = J('.addtr').length
        let sum = 0
        for (let i = 0; i < tr_num; i++) {
          if (type == 1) {
            let val = J('.addtr').eq(i).find('td').eq(5).find('input').val()
            sum += val * 1
          } else if (type == 2) {
            let val = J('.addtr').eq(i).find('td').eq(9).find('input').val()
            sum += val * 1
          }
        }
        J('input[name=' + name + ']').val(sum.toFixed(2))
      }
    }
  }
</script>

<style scoped>
  .p2 {
    text-align: center;
  }

  .pp1 {
    font-weight: bold;
    font-size: 24px;
  }

  .yhs input {
    width: 100%;
    height: 100%;
    border: 0px;
  }
</style>



